ul.linklist  {
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    width: 50vw;
    margin-right: 20px;
    margin-left: 20px;
    list-style: none;
    text-align: center;
}

ul.sociallinklist {
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    list-style: none;
    text-align: center;
}

.social-media{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 30vh;
    background-color: hsl(0, 0%, 95%);
}

.sociallinks{
    display: flex;
}

.links {
    display: flex;
    justify-content: center;
    padding: 2vh;
    background-color: hsl(0, 0%, 95%);
}

.fab{
    cursor: pointer;
}